
@import url(../../CSS/normalize.css);
html {
  width: 100%;
  height: 100%;
  background: #f0f0f0;
}
* {
  font-family: "Microsoft YaHei", Arial, sans-serif;
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
}
li {
  list-style: none;
}
input {
  border: 0;
  outline: none;
}
input:focus {
    outline: none;
}
button {
  border: 0;
}
a {
  text-decoration: none;
}
svg {
  vertical-align: middle;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
  overflow: hidden;
}
.clearfix {
  zoom: 1;
}
body {
  background: #f0f0f0;
}
#app {
  background: #f0f0f0;
}
.lv-container {
  width: 1214px;
  min-width: 1213px;
  margin: 0 auto;
  overflow: hidden;
}
.side-nav {
  position: fixed;
  left: 0;
  top: 90px;
}
.main {
  width: 1200px;
  margin: 0 auto;
  *zoom: 1;
}
.main:before, .main:after {
  content: "";
  display: table;
}
.main:after {
  clear: both;
}
.search-container {
  margin-top: 20px;
}
.search-container .search-title {
    font-size: 20px;
    font-weight: normal;
}
.category {
  margin: 10px 0;
}
.result-item {
  display: inline-block;
  float: left;
  width: 220px;
  height: 300px;
  margin-right: 20px;
  margin-bottom: 20px;
  color: #4d4d4d;
  background: #fff;
  border-radius: 6px;
  cursor: pointer;
}
.result-item .result-cover {
    width: 220px;
    height: 150px;
}
.result-item .result-cover img {
      width: 100%;
      height: 100%;
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
}
.result-item .result-title {
    padding: 14px 17px 2px 17px;
    font-size: 14px;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.result-item .result-teacher {
    padding-right: 20px;
    margin-top: 5px;
    font-size: 12px;
    text-align: right;
    color: #396;
}
.result-item .result-teacher svg {
      margin-right: 2px;
      vertical-align: text-bottom;
}
.result-item .result-summary {
    height: 82px;
    padding: 5px 19px;
    font-size: 13px;
    color: #989898;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
}



